* {
  margin: 0;
  padding: 0;
}


article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden] {
  display: none;
}

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

a:focus {
  outline: thin dotted;
}

a:active,
a:hover {
  outline: 0;
}

h1 {
  font-size: 2em;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: 700;
}

dfn {
  font-style: italic;
}

mark {
  background: #ff0;
  color: #000;
}

code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}

pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

q {
  quotes: \201C \201D \2018 \2019;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -.5em;
}

sub {
  bottom: -.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button,
input {
  line-height: normal;
}

button,
html input[type=button],
/* 1 */
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
input[disabled] {
  cursor: default;
}

input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body,
figure {
  margin: 0;
}

legend,
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0;
}

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(00 0 0);
  overflow: hidden;
}



.wrapper {
  width: 80%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.heroImage {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  line-height: 120px;
  background-image: linear-gradient(to bottom, #ff7a4d, #ff8749, #ff9445, #ffa043, #ffad42, #ffb844, #ffc247, #fecd4c, #fdd854, #fce35d, #fbed66, #f9f871);
}





.App {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: center;

}

.App h1 {
  font-size: 4rem;
  font-family: 'Permanent Marker',
    cursive;
}

.App h2 {
  font-size: 2rem;
  font-family: 'Raleway',
    sans-serif;
}


.App h3 {
  font-size: 1.5rem;
  font-family: 'Raleway',
    sans-serif;
  padding: 2%;
  color: yellow;
  text-transform: uppercase;
}

.gallery {
  width: 100%;
  background-image: linear-gradient(to left top, #641fd5, #0f51e4, #006de5, #0082dc, #1293ce, #3e8ebf, #528ab0, #6085a1, #556d8c, #4b5676, #42405f, #382b47);
}

.imageBox {
  width: calc(50% - 2rem - 4px);
  border: 5px dotted goldenrod;
  margin: 5% auto;
  text-align: center;
  border-radius: 5%;
  padding: 1%;
}

.App img {
  width: 100%;
}

.button {
  width: 25%;
  line-height: 5%;
  padding: 3% 0;
  margin: 3% 0;
  border-radius: 25%;
  font-family: 'Raleway',
    sans-serif;
  font-size: 1rem;
}




@media (max-width: 888px) {

  .wrapper {
    flex-direction: column;
  }

  .imageBox {
    width: 100%;
  }

}

@media (max-width: 725px) {
  .App h2 {

    font-size: 1.75rem;
  }
}

@media (max-width: 637px) {
  .heroImage {
    line-height: 100px;
  }

  .App h2 {

    font-size: 1.5rem;

  }
}

@media (max-width: 637px) {

  .App h2 {

    font-size: 1.25rem;

  }

}

@media (max-width: 464px) {

  .App h2 {

    font-size: 1rem;

  }

  .App h1 {

    font-size: 2.5rem;
  }

  .button {

    width: 50%;
  }


}